.register {
  height: calc(100vh - 50px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(
    rgba(255, 255, 255, 0.5),
    rgba(255, 255, 255, 0.5)
  ),
    url("https://images.unsplash.com/photo-1485988412941-77a35537dae4?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1196&q=80");
  background-size: cover;
}

.registerTitle{
  font-size: 50px;
}

.registerForm{
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  position: relative;
}

.registerForm > label{
  margin: 10px 0;
}

.registerInput{
  padding: 10px;
  background-color: white;
  border: none;
}

.registerButton{
  margin-top: 20px;
  padding: 10px;
  cursor: pointer;
  background-color: teal;
  border: none;
  color: rgb(255, 255, 255);
  border-radius: 10px;
}

.registerLoginButton{
  position: absolute;
  top: 60px;
  right: 20px;
  background-color: lightcoral;
  color: white;
  cursor: pointer;
  border: none;
  padding: 10px;
  border-radius: 10px;
}